import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { SQLite, SQLiteObject } from '@ionic-native/sqlite';
import { Toast } from '@ionic-native/toast';



import {Validators, FormBuilder, FormGroup } from '@angular/forms';
import { FormsModule, ReactiveFormsModule }   from '@angular/forms';
import { AbstractControl } from '@angular/forms';

import {FormControl} from "@angular/forms";

/**
 * Generated class for the EditPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@Component({
  selector: 'page-edit',
  templateUrl: 'edit.html',
})
export class EditPage {


  formGroupEdit : FormGroup;
  rowid = 0;

  constructor(public navCtrl: NavController,
    public navParams: NavParams, private formBuilder: FormBuilder, 
    private sqlite: SQLite,
    private toast: Toast) {



    this.navCtrl = navCtrl;
    this.formGroupEdit = this.formBuilder.group({
      name: ['', Validators.required],
      location: ['', Validators.required],
      star: ['5', Validators.required],
      price: ['3', Validators.required],
      type: ['africaine', Validators.required],
      review: ['', Validators.required],
      //file: ['', Validators.required]


    });

      //met à jour avec les données courantes de la bdd
      this.rowid = navParams.get("rowid");
      this.getCurrentData(navParams.get("rowid"));
  }



  getCurrentData(rowid) {
    this.sqlite.create({
      name: 'ionicdb.db',
      location: 'default'
    }).then((db: SQLiteObject) => {



      db.executeSql('SELECT * FROM restaurants WHERE rowid=?', [rowid])
        .then(res => {
          if(res.rows.length > 0) {
            //this.data.rowid = res.rows.item(0).rowid;
            this.formGroupEdit.value.name = res.rows.item(0).name;
            this.formGroupEdit.value.location = res.rows.item(0).location;
            this.formGroupEdit.value.star = res.rows.item(0).star;
            this.formGroupEdit.value.price = res.rows.item(0).price;
            this.formGroupEdit.value.type = res.rows.item(0).type;
            this.formGroupEdit.value.review = res.rows.item(0).review;
          }
        })
        .catch(e => {
          console.log(e);
          this.toast.show(e, '5000', 'center').subscribe(
            toast => {
              console.log(toast);
            }
          );
        });
    }).catch(e => {
      console.log(e);
      this.toast.show(e, '5000', 'center').subscribe(
        toast => {
          console.log(toast);
        }
      );
    });
  }

  updateData() {
    this.sqlite.create({
      name: 'ionicdb.db',
      location: 'default'
    }).then((db: SQLiteObject) => {

      db.executeSql('UPDATE restaurants SET name=?,location=?,star=?,price=?,type=?,review=?  WHERE rowid=?',
      [this.formGroupEdit.value.name,this.formGroupEdit.value.location,this.formGroupEdit.value.star,this.formGroupEdit.value.price,this.formGroupEdit.value.type,this.formGroupEdit.value.review,this.rowid])
        .then(res => {
          console.log(res);
          this.toast.show('Data updated', '5000', 'center').subscribe(
            toast => {
              this.navCtrl.popToRoot();
            }
          );
        })
        .catch(e => {
          console.log(e);
          this.toast.show(e, '5000', 'center').subscribe(
            toast => {
              console.log(toast);
            }
          );
        });
    }).catch(e => {
      console.log(e);
      this.toast.show(e, '5000', 'center').subscribe(
        toast => {
          console.log(toast);
        }
      );
    });
  }



  ionViewDidLoad() {
    console.log('ionViewDidLoad EditPage');
  }




}



